  <h1 id="content-start">Getting Start Here!</h1>
  <p>This is an open-sourced project that allows the user to easily modify and manage their .LRC (lyrics) files~!</p>

  <p>Users should have a corresponding music player so that their lyrics files can be displayed (such as foobar2000, FiiO Music App, or others).</p>
  <ul>
    <li>In general, your .LRC file and the song (.MP3/.FLAC/.WAV etc.) file should have the same name and be put under the same directory.</li>
    <li>For details, please refer to your music player lyrics display instruction/documentation.</li>
  </ul>

  <!--Second feature divider-->
  <h1>Important Note!</h1>
  <ul>
    <li><b>Users need to download runtime on their computer before using the .JAR application (at least for the current version).</b></li>
    <li><a href="https://github.com/sonikkk-526/Lyrics-Editor-for-.LRC/releases/tag/v1.2.0" target="_blank">Current version</a> can be found here!</li>
  </ul>
  
